@import "../global.scss";

.Education{
    margin-bottom: 100px;
    @include mobile{
        padding-top: 52px;
    }
    .topEducation{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        // height: calc(100vh - 400px);
        margin-bottom: 60px;
        @include mobile{
            flex-direction: column-reverse;
        }
        
    
        .lefttopEdu{
            flex: 5;
        }
        .righttopEdu{
            flex: 5;
            display: flex;
            width: 100%;
            height: 100%;
            @include mobile{
                align-items: center;
                justify-content: center;
            }
            .rtdWrper{
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
    
                
                h3{
                    font-size: 2.5rem;
                    color: rgb(202, 202, 202);
                    margin-bottom: 5px;

                    @include mobile{
                        font-size: 1.7rem;
                        margin-bottom: 20px;
                    }
                }
                div{
                    color: rgb(199, 199, 199);
                    font-size: 1.1rem;

                    @include mobile{
                        font-size: 0.9rem;
                        text-align: center;
                    }
                }
            }
    
        }
    }
    .bottomEducation{
        .mainedu{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;

            @include lPY{
                flex-wrap: nowrap;
                flex-direction: column;
            }
            .edubox{
                width: 31%;
                background-color: #d6c68f;
                height: 15.5rem;
                border-radius: 20px;
                margin: 0px 10px;
                padding-left: 8px;

                @include lPY{
                    width: 70%;
                    margin-bottom: 10px;
                }
                @include mobile{
                    width: 95%;
                }
                
                .timepred{
                    float: right;
                    clear: both;
                    font-size: 17px;
                    background-color: aquamarine;
                    border-radius: 10px;
                    padding: 7px;

                    @include mobile{
                        font-size: 15px;
                        padding: 5px;
                    }
                }
                .instName{
                    font-size: 23.5px;
                    color: #414141;
                    clear: both;
                    display: flex;
                    flex-direction: column;
                    // margin-top: 40px;
                    margin-bottom: 12px;

                    @include mobile{
                        font-size: 20px;
                    }
                }
                .eduDesc{
                    font-size: 20px;
                    color: #666666;
                    @include mobile{
                        font-size: 18px;
                    }
                }
                .eduLists{
                    font-size: 18px;
                    color: #474747;
                    .eduListDesc{
                        margin-bottom: 5px;
                    }
                    @include mobile{
                        font-size: 16px;
                    }
                }
            }

        }
    }
}